<template>
  <div>
    <h1>내 캐릭터 등록하기</h1>
    <h3>파티를 구하고 있다는 내용을 등록합니다.</h3>
    <div id="character">
        <div><label for="world">서버 <select id="world" v-model="world">
            <option value="">선택</option>
            <option :id="'server-' + key" v-for="(server, key) in serverList" :key="server.engName" :value="server.engName">{{server.korName}}</option>
        </select></label></div>
        <div><label for="nickname">캐릭터 명 <input type="text" name="nickname" id="nickname" placeholder="캐릭터 명" v-model="nickname" maxlength="9" autocomplete="off"></label></div>
    </div>
    <div id="raid">
        <p>■ 참여를 원하는 레이드</p>
        <div><label for="raids"><select id="raids" v-model="raid">
            <option value="">선택</option>
            <option :id="'raid-' + key" v-for="(raid, key) in raidList" :key="raid.engName" :value="raid.engName">{{raid.korName}}</option>
        </select></label></div>
    </div>
    <div id="job-main" class="job">
        <p>■ 주 직업</p>
        <div :id="'job-' + key" v-for="(jobs, key) in jobList" :key="key">
            <div>
                <div v-for="job in jobs" :key="job.engName" :class="{ checked: job.engName == mainJob }">
                    <label><input type="radio" :value="job.engName" :class="job.engName" name="job-main" v-on:click="mainJob = $event.target.value">{{ job.korName }}</label>
                </div>
            </div>
        </div>
    </div>
    <div id="job-sub" class="job">
        <p>■ 참여 가능한 부직업 (선택 안해도 무방)</p>
        <div :id="'job-' + key" v-for="(jobs, key) in jobList" :key="key">
            <div>
                <div v-for="job in jobs" :key="job.engName" :class="{ checked: checkedSubJobs.indexOf(job.engName) != -1, mainJob: job.engName == mainJob }">
                    <label><input type="checkbox" :value="job.engName" :class="job.engName" v-model="checkedSubJobs">{{ job.korName }}</label>
                </div>
            </div>
        </div>
    </div>
    <div id="agreement">
        <p>■ 캐릭터 정보 표시 동의</p>
        등록시 사사게 검색 결과 페이지 링크가 함께 등록됩니다.
        <a :href="sasagaeURL(nickname)" target="_blank" v-on:click="openSasagae($event)">확인하기</a><br>
        등록시 해당 캐릭터의 FFLog 기록을 표시함에 동의합니다.
        <a :href="FFLogURL(world, nickname)" target="_blank" v-on:click="openFFLog($event)">로그 확인하기</a>
        <label class="agreement"><input type="checkbox" name="agreeFFLog">필수</label>
    </div>
    <div id="etc">
        <p>■ 기타 전달하고 싶은 내용 (최대 {{maxEtcChar}}자)</p>
        <h5>사사게에 본인 닉네임이 등록된 특이한 사유가 있다거나<br>
        참여 가능한 잡들의 숙련도 정도같은 사항을 작성해주세요.</h5>
        <textarea name="etc" cols="46" rows="10" v-model="etc"></textarea>
        <div class="text-length-view">글자 수: {{etc.length}} / {{maxEtcChar}}</div>
    </div>
    <div id="contact">
        <p>■ 연락처 (최소 1개 선택)</p>
        <h5>기타 칸에 전화번호를 입력하면 다른 사람이 파싱할 수 있으므로<br>
        중요한 개인정보는 입력하지 않기를 강력히 권장합니다.</h5>
        <div class="openkakao">
          <input type="checkbox" id="openkakao" value="openkakao" v-model="contactList" v-on:click="toggleInput($event.target)">
          <label for="openkakao">오픈 카카오톡 링크 </label>
          <input type="text" autocomplete="off" v-model="openkakao" placeholder="https://open.kakao.com/o/" maxlength="35" disabled>
        </div>
        <div id="checkOpenkakao" class="checkContactOk display-none"><div>&#x2714;오픈 카카오톡 링크 확인</div></div>
        <div class="discord">
          <input type="checkbox" id="discord" value="discord" v-model="contactList" v-on:click="toggleInput($event.target)">
          <label for="discord">디스코드 DM / 서버 </label>
          <input type="text" autocomplete="off" v-model="discord" placeholder="사용자명#0000 / 서버 초대 주소" disabled>
        </div>
        <div id="checkDiscord" class="checkContactOk display-none"><div></div></div>
        <div class="contact-etc">
          <input type="checkbox" id="contact-etc" value="etc" v-model="contactList" v-on:click="toggleInput($event.target)">
          <label for="contact-etc">기타 (자유롭게 입력) </label>
          <input type="text" autocomplete="off" v-model="contactEtc" disabled>
        </div>
        <h5 class="no-margin"><span id="noPhone" class="red display-none">부득이하게 전화번호를 사용해야 할 경우 기타 칸에 아무내용이나 입력하고 <br>
        위의 기타 전달하고 싶은 내용 부분에 작성하기를 권장합니다.</span></h5>
    </div>
    <div id="password">
        <p>■ 게시글 수정 / 삭제를 위한 비밀번호 설정 (4-15자)</p>
        <h5>가능하면 다른 웹사이트에서 사용하지 않는 비밀번호를 입력해주세요.<br><span class="red">현재 비밀번호는 평문으로 저장되고 있습니다.</span></h5>
        <div><label for="passwordInput">비밀번호</label> <input type="password" name="passwordInput" id="passwordInput" placeholder="비밀번호" maxlength="15"></div>
        <div><label for="passwordCheck">비밀번호 확인</label> <input type="password" name="passwordCheck" id="passwordCheck" placeholder="비밀번호 확인" maxlength="15"></div>
    </div>
    <div id="serviceAgreement">
        <p>■ 서비스 이용 동의</p>
        익명성을 이용한 기능 악용시 IP밴을 위한 IP수집에 동의합니다. <label class="agreement"><input type="checkbox" name="agreeIP">필수</label><br>
    </div>
    <div id="register">
      <div id="registerMessage"></div><br>
      <div v-on:click="register()" id="registerButton">등록하기</div>
    </div>
  </div>
</template>

<script>
import config from '../config.js';
import { mapState } from 'vuex';
export default {
  name: 'newMember',
  data () {
    return {
      world: '',
      raid: '',
      nickname: '',
      mainJob: null,
      checkedSubJobs: [],
      etc: '',
      contactList: [],
      openkakao: '',
      discord: '',
      contactEtc: '',
      animationTimeout: null,

      // from config.js
      serverList: config.serverList,
      jobList: config.jobList,
      raidList: config.raidList,
      sasagaeURL: config.sasagaeURL,
      FFLogURL: config.FFLogURL,
      maxEtcChar: config.maxMemberEtcChar,
      nicknameRegex: config.nicknameRegex,
      openkakaoRegex: config.openkakaoRegex,
      discordIDRegex: config.discordIDRegex,
      discordServerRegex: config.discordServerRegex,
      phoneNumRegex: config.phoneNumRegex,
    }
  },
  computed: {
    // ...mapState({
    //   a: state => state.a,
    // })
  },
  watch: {
    openkakao() {
      if (this.openkakao.match(this.openkakaoRegex))
        document.querySelector('#checkOpenkakao').classList.remove('display-none');
      else
        document.querySelector('#checkOpenkakao').classList.add('display-none');
    },
    discord() {
      if (this.discord.match(this.discordIDRegex)){
        document.querySelector('#checkDiscord').classList.remove('display-none');
        document.querySelector('#checkDiscord > div').innerHTML = '&#x2714;디스코드 ID 확인';
      }
      else if (this.discord.match(this.discordServerRegex)){
        document.querySelector('#checkDiscord').classList.remove('display-none');
        document.querySelector('#checkDiscord > div').innerHTML = '&#x2714;디스코드 서버 초대 주소 확인';
      }
      else
        document.querySelector('#checkDiscord').classList.add('display-none');
    },
    contactEtc() {
      if (this.contactEtc.match(this.phoneNumRegex))
        document.querySelector('#noPhone').classList.remove('display-none');
      else
        document.querySelector('#noPhone').classList.add('display-none');
    },
  },
  methods: {
    openSasagae(event){
      event.preventDefault();
      if (this.nickname.match(this.nicknameRegex) === null) {
        alert('올바른 캐릭터 명 입력 후 확인 가능합니다.');
        return;
      }
      window.open(event.target.href);
    },
    openFFLog(event){
      event.preventDefault();
      if (this.world === '' || this.nickname.match(this.nicknameRegex) === null) {
        alert('서버를 선택하고 올바른 캐릭터 명 입력 후 확인 가능합니다.');
        return;
      }
      window.open(event.target.href);
    },
    toggleInput(target){
      document.querySelector(`#${target.id} + label + input`).disabled = !target.checked;
    },
    register(){
      let alertMessage = '';
      if (this.world === '') alertMessage += '서버를 선택하세요.<br>';
      if (this.nickname.match(this.nicknameRegex) === null) alertMessage += '올바른 캐릭터 명을 입력하세요. (대소문자 구분)<br>';
      if (this.raid === '') alertMessage += '레이드를 선택하세요.<br>';
      if(this.mainJob === null) alertMessage += '주 직업을 선택하세요.<br>';
      if (!document.querySelector('[name=agreeFFLog]').checked) alertMessage += 'FFLog 표시에 동의하지 않으면 등록할 수 없습니다.<br>';
      if (this.etc.length > this.maxEtcChar) alertMessage += `기타 내용 입력란은 최대 ${this.maxEtcChar}자까지 허용됩니다.<br>`
      if (this.contactList.length == 0) alertMessage += '연락처를 최소한 1개 선택해 주세요.<br>';
      if (document.querySelector('input#openkakao').checked && !this.openkakao.match(this.openkakaoRegex)) alertMessage += '올바른 오픈카카오톡 링크를 입력하세요.<br>';
      if (document.querySelector('input#discord').checked && (!this.discord.match(this.discordServerRegex) && !this.discord.match(this.discordIDRegex))) alertMessage += '올바른 디스코드 정보를 입력하세요. (사용자명#0000 혹은 서버 초대 주소)<br>';
      if (document.querySelector('input#contact-etc').checked && this.contactEtc == '') alertMessage += '연락처를 입력해주세요.<br>';
      if (!document.querySelector('#passwordInput').value) alertMessage += '비밀번호를 입력하세요.<br>';
      else if (document.querySelector('#passwordInput').value.length < 4 || document.querySelector('#passwordInput').value.length > 15) alertMessage += '비밀번호 길이를 확인하세요.<br>';
      if (document.querySelector('#passwordInput').value != document.querySelector('#passwordCheck').value) alertMessage += '비밀번호와 비밀번호 확인이 다릅니다.<br>';
      if (!document.querySelector('[name=agreeIP]').checked) alertMessage += 'IP 수집에 동의하지 않으면 등록할 수 없습니다.<br>';
      if (alertMessage != '') {
        document.querySelector('#registerMessage').innerHTML = alertMessage;

        // Start Animation
        clearTimeout(this.animationTimeout);
        document.querySelector('#registerMessage').classList.remove('alertRed');
        document.querySelector('#registerMessage').classList.add('alertRed');
        this.animationTimeout = setTimeout(() => {
          document.querySelector('#registerMessage').classList.remove('alertRed');
        }, 800);
        return;
      }
      document.querySelector('#registerMessage').innerHTML = '';
      alert('이제 서버로 전송할 차례 (미구현)');
    },
  }
}
</script>

<style src="./css/base.css"></style>
<style src="./css/new.css"></style>
<style scoped>
</style>



